.mb-0 {
  margin-bottom: 0;
}

html {
  box-sizing: border-box;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 1rem;
  line-height: 1.5;
  overflow-y: scroll;
  min-height: 100%;
  word-wrap: break-word;
}

.h1, h1 {
  font-size: 3rem;
  font-weight: 800;
  line-height: 1.2;
  margin-bottom: 1rem;
}
@media only screen and (max-width: 767px) {
  .h1, h1 {
    font-size: 2.4rem;
  }
}

.h2, h2 {
  font-size: 2rem;
  font-weight: 800;
  line-height: 1.2;
  margin-bottom: 1rem;
}
@media only screen and (max-width: 767px) {
  .h2, h2 {
    font-size: 1.8rem;
  }
}

.h3, h3 {
  font-size: 1.5rem;
  font-weight: 600;
  line-height: 1.2;
  margin-bottom: 1rem;
}
@media only screen and (max-width: 767px) {
  .h3, h3 {
    font-size: 1.3rem;
  }
}

.p, p {
  margin: 0 0 1.2rem 0;
}

p.t--leadin {
  font-size: 1.2rem;
  font-weight: 600;
}
p.t--leadin::first-letter {
  initial-letter: 2 1;
}
@media only screen and (max-width: 767px) {
  p.t--leadin::first-letter {
    initial-letter: 1 1;
  }
}

a {
  color: #1D4ED8;
  transition: all 0.3s ease-in-out;
}
a:hover {
  text-decoration: underline;
  color: #6f90ec;
}

blockquote {
  background: #edf1fd;
  margin: 0;
  padding: 0.4rem;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
ul li {
  margin-bottom: 0.4rem;
}
ul li::before {
  content: "â€¢ ";
  color: #1D4ED8;
}

.t--uppercase {
  text-transform: uppercase;
}

header {
  border-bottom: 1px solid #1D4ED8;
  padding: 1rem 0;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2.5rem;
}
@media only screen and (max-width: 767px) {
  .container {
    padding: 0 1rem;
  }
}

main {
  display: flex;
}
@media only screen and (max-width: 767px) {
  main {
    flex-direction: column;
  }
}

section {
  width: 75%;
  padding-right: 2rem;
  padding-bottom: 4rem;
}
@media only screen and (max-width: 767px) {
  section {
    width: 100%;
    padding-right: 0;
    padding-bottom: 2rem;
  }
}

.article {
  display: flex;
}
@media only screen and (max-width: 767px) {
  .article {
    flex-direction: column;
  }
}
.article__content {
  width: 70%;
  padding-right: 1rem;
}
@media only screen and (max-width: 767px) {
  .article__content {
    width: 100%;
    padding-right: 0;
  }
}
.article__image {
  width: 30%;
}
@media only screen and (max-width: 767px) {
  .article__image {
    width: 100%;
    margin-top: 1rem;
  }
}
.article__image img {
  display: block;
  width: 100%;
}

aside {
  width: 25%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-left: 1px solid #1D4ED8;
  padding-left: 2rem;
  padding-bottom: 2rem;
}
@media only screen and (max-width: 767px) {
  aside {
    width: 100%;
    padding-left: 0;
    border-top: 1px solid #1D4ED8;
    border-left: 0;
  }
}
@media only screen and (max-width: 767px) {
  aside .sidebar {
    margin-bottom: 2rem;
  }
}

footer {
  border-top: 1px dashed #1D4ED8;
  background: #edf1fd;
  display: flex;
  align-items: center;
  padding: 2rem 0;
}

.navbar {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media only screen and (max-width: 767px) {
  .navbar {
    flex-direction: column;
    align-items: flex-start;
  }
}
.navbar .burger-menu {
  display: none;
  position: absolute;
  right: 0;
  top: 1rem;
}
.navbar .burger-menu .line {
  width: 24px;
  height: 3px;
  background-color: #000;
  margin: 4px;
  transition: ease-in-out 0.3s;
}
.navbar .burger-menu.active .line:nth-child(1) {
  transform: rotate(-45deg) translate(-4px, 5px);
}
.navbar .burger-menu.active .line:nth-child(2) {
  opacity: 0;
}
.navbar .burger-menu.active .line:nth-child(3) {
  transform: rotate(45deg) translate(-5px, -6px);
}
@media only screen and (max-width: 767px) {
  .navbar .burger-menu {
    display: block;
  }
}
.navbar .main-nav__menu {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}
.navbar .main-nav__menu.show {
  display: block;
}
@media only screen and (max-width: 767px) {
  .navbar .main-nav__menu.show {
    margin-top: 2rem;
  }
}
@media only screen and (max-width: 767px) {
  .navbar .main-nav__menu {
    display: none;
  }
}
.navbar .main-nav__menu li {
  margin-left: 1.5rem;
}
@media only screen and (max-width: 767px) {
  .navbar .main-nav__menu li {
    margin-left: 0;
  }
}
.navbar .main-nav__menu li:first-child {
  margin-left: 0;
}
.navbar .main-nav__menu li a {
  text-decoration: none;
}
.navbar .main-nav__menu li::before {
  content: "";
}

.button {
  background: linear-gradient(0deg, #D3E2FC 0%, #2A56D1 100%);
  color: #fff;
  text-decoration: none;
  position: relative;
  align-self: start;
  border-radius: 6px;
  z-index: 1;
  border: 1.5px solid white;
  box-shadow: 0 0 3px 2px rgba(29, 78, 216, 0.749);
  padding: 2px 10px;
  font-size: 14px;
  transition: all 0.3s ease-in-out;
  font-weight: 800;
}
.button::before {
  content: "";
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
  border: 1.5px solid white;
  border-radius: 8px;
  z-index: 0;
  box-shadow: 0 0 3px 2px rgba(29, 78, 216, 0.749);
}
.button:hover {
  box-shadow: 0 0 3px 6px rgba(29, 78, 216, 0.749);
  color: #fff;
  text-decoration: none;
}